	module.exports = {"data":{"postsJson":{"title":"The Ultimate Sign: Christ's LIfe-giving Resurrection!","questionsTitle":"COMMANDS & PROMISES","display":"block","bookName":"John","ref":"20:1-21:25","html":"<p>   <strong>20</strong><u>1</u>Now on the first day of the week, Mary Magdalene went early, while it was still dark, to the tomb, and saw the stone taken away from the tomb. <u>2</u>Therefore she ran and came to Simon Peter, and to the other disciple whom Jesus loved, and said to them,</p><h1>MARY MAGDALENE</h1><h4>They have taken away the Lord out of the tomb, and we don’t know where they have laid him!</h4><p>   <u>3</u>Therefore Peter and the other disciple went out, and they went toward the tomb. <u>4</u>They both ran together. The other disciple outran Peter, and came to the tomb first. <u>5</u>Stooping and looking in, he saw the linen cloths lying, yet he didn’t enter in. <u>6</u>Then Simon Peter came, following him, and entered into the tomb. He saw the linen cloths lying, <u>7</u>and the cloth that had been on his head, not lying with the linen cloths, but rolled up in a place by itself. <u>8</u>So then the other disciple who came first to the tomb also entered in, and he saw and believed. <u>9</u>For as yet they didn’t know the Scripture, that he must rise from the dead. <u>10</u>So the disciples went away again to their own homes. <br />   <u>11</u>But Mary was standing outside at the tomb weeping. So, as she wept, she stooped and looked into the tomb, <u>12</u>and she saw two angels in white sitting, one at the head, and one at the feet, where the body of Jesus had lain. <u>13</u>They told her,</p><h1>TWO ANGELS</h1><h4>Woman, why are you weeping?</h4><p>   She said to them,</p><h1>MARY MAGDALENE</h1><h4>Because they have taken away my Lord, and I don’t know where they have laid him.</h4><p><u>14</u>When she had said this, she turned around and saw Jesus standing, and didn’t know that it was Jesus. <br />   <u>15</u>Jesus said to her,</p><h1>JESUS</h1><h2>Woman, why are you weeping? Who are you looking for?</h2><p>   She, supposing him to be the gardener, said to him,</p><h1>MARY MAGDALENE</h1><h4>Sir, if you have carried him away, tell me where you have laid him, and I will take him away.</h4><p>   <u>16</u>Jesus said to her,</p><h1>JESUS</h1><h2>Mary.</h2><p>   She turned and said to him,</p><h1>MARY MAGDALENE</h1><h4>Rabboni!*</h4><p>which is to say, “Teacher!”* <br />   <u>17</u>Jesus said to her,</p><h1>JESUS</h1><h2>Don’t hold me, for I haven’t yet ascended to my Father; but go to my brothers, and tell them, ‘I am ascending to my Father and your Father, to my God and your God.’</h2><p>   <u>18</u>Mary Magdalene came and told the disciples that she had seen the Lord, and that he had said these things to her. <u>19</u>When therefore it was evening, on that day, the first day of the week, and when the doors were locked where the disciples were assembled, for fear of the Jews, Jesus came and stood in the middle, and said to them,</p><h1>JESUS</h1><h2>Peace be to you.</h2><p>   <u>20</u>When he had said this, he showed them his hands and his side. The disciples therefore were glad when they saw the Lord. <u>21</u>Jesus therefore said to them again,</p><h1>JESUS</h1><h2>Peace be to you. As the Father has sent me, even so I send you.</h2><p><u>22</u>When he had said this, he breathed on them, and said to them,</p><h1>JESUS</h1><h2>Receive the Holy Spirit! <u>23</u>If you forgive anyone’s sins, they have been forgiven them. If you retain anyone’s sins, they have been retained.</h2><p>   <u>24</u>But Thomas, one of the twelve, called Didymus, wasn’t with them when Jesus came. <u>25</u>The other disciples therefore said to him,</p><h1>TEN DISCIPLES</h1><h3>We have seen the Lord!</h3><p>   But he said to them,</p><h1>THOMAS</h1><h3>Unless I see in his hands the print of the nails, and put my hand into his side, I will not believe.</h3><p>   <u>26</u>After eight days again his disciples were inside, and Thomas was with them. Jesus came, the doors being locked, and stood in the middle, and said,</p><h1>JESUS</h1><h2>Peace be to you.</h2><p><u>27</u>Then he said to Thomas,</p><h1>JESUS</h1><h2>Reach here your finger, and see my hands. Reach here your hand, and put it into my side. Don’t be unbelieving, but believing.</h2><p>   <u>28</u>Thomas answered him,</p><h1>THOMAS</h1><h3>My Lord and my God!</h3><p>   <u>29</u>Jesus said to him,</p><h1>JESUS</h1><h2>Because you have seen me,* you have believed. Blessed are those who have not seen, and have believed.</h2><p>   <u>30</u>Therefore Jesus did many other signs in the presence of his disciples, which are not written in this book; <u>31</u>but these are written, that you may believe that Jesus is the Christ, the Son of God, and that believing you may have life in his name. <br />   <strong>21</strong><u>1</u>After these things, Jesus revealed himself again to the disciples at the sea of Tiberias. He revealed himself this way. <u>2</u>Simon Peter, Thomas called Didymus, Nathanael of Cana in Galilee, and the sons of Zebedee, and two others of his disciples were together. <u>3</u>Simon Peter said to them,</p><h1>SIMON PETER</h1><h3>I’m going fishing.</h3><p>   They told him,</p><h1>SIX DISCIPLES</h1><h3>We are also coming with you.</h3><p>They immediately went out, and entered into the boat. That night, they caught nothing. <u>4</u>But when day had already come, Jesus stood on the beach, yet the disciples didn’t know that it was Jesus. <u>5</u>Jesus therefore said to them,</p><h1>JESUS</h1><h2>Children, have you anything to eat?</h2><p>   They answered him,</p><h1>SEVEN DISCIPLES</h1><h3>No</h3><p><u>6</u>He said to them,</p><h1>JESUS</h1><h2>Cast the net on the right side of the boat, and you will find some.</h2><p>   They cast it therefore, and now they weren’t able to draw it in for the multitude of fish. <u>7</u>That disciple therefore whom Jesus loved said to Peter,</p><h1>JOHN THE DISCIPLE</h1><h3>It’s the Lord!</h3><p>   So when Simon Peter heard that it was the Lord, he wrapped his coat around him (for he was naked), and threw himself into the sea. <u>8</u>But the other disciples came in the little boat (for they were not far from the land, but about two hundred cubits* away), dragging the net full of fish. <u>9</u>So when they got out on the land, they saw a fire of coals there, and fish laid on it, and bread. <u>10</u>Jesus said to them,</p><h1>JESUS</h1><h2>Bring some of the fish which you have just caught.</h2><p>   <u>11</u>Simon Peter went up, and drew the net to land, full of great fish, one hundred fifty-three; and even though there were so many, the net wasn’t torn. <br />   <u>12</u>Jesus said to them,</p><h1>JESUS</h1><h2>Come and eat breakfast.</h2><p>   None of the disciples dared inquire of him, “Who are you?” knowing that it was the Lord. <br />   <u>13</u>Then Jesus came and took the bread, gave it to them, and the fish likewise. <u>14</u>This is now the third time that Jesus was revealed to his disciples, after he had risen from the dead. <u>15</u>So when they had eaten their breakfast, Jesus said to Simon Peter,</p><h1>JESUS</h1><h2>Simon, son of Jonah, do you love me more than these?</h2><p>   He said to him,</p><h1>SIMON PETER</h1><h3>Yes, Lord; you know that I have affection for you.</h3><p>   He said to him,</p><h1>JESUS</h1><h2>Feed my lambs.</h2><p><u>16</u>He said to him again a second time,</p><h1>JESUS</h1><h2>Simon, son of Jonah, do you love me?</h2><p>   He said to him,</p><h1>SIMON PETER</h1><h3>Yes, Lord; you know that I have affection for you.</h3><p>   He said to him,</p><h1>JESUS</h1><h2>Tend my sheep.</h2><p><u>17</u>He said to him the third time,</p><h1>JESUS</h1><h2>Simon, son of Jonah, do you have affection for me?</h2><p>   Peter was grieved because he asked him the third time, “Do you have affection for me?” He said to him,</p><h1>SIMON PETER</h1><h3>Lord, you know everything. You know that I have affection for you.</h3><p>   Jesus said to him,</p><h1>JESUS</h1><h2>Feed my sheep. <u>18</u>Most certainly I tell you, when you were young, you dressed yourself, and walked where you wanted to. But when you are old, you will stretch out your hands, and another will dress you, and carry you where you don’t want to go.</h2><p>   <u>19</u>Now he said this, signifying by what kind of death he would glorify God. When he had said this, he said to him,</p><h1>JESUS</h1><h3>Follow me.</h3><p>   <u>20</u>Then Peter, turning around, saw a disciple following. This was the disciple whom Jesus loved, the one who had also leaned on Jesus’ breast at the supper and asked, “Lord, who is going to betray You?” <u>21</u>Peter seeing him, said to Jesus,</p><h1>SIMON PETER</h1><h3>Lord, what about this man?</h3><p>   <u>22</u>Jesus said to him,</p><h1>JESUS</h1><h2>If I desire that he stay until I come, what is that to you? You follow me.</h2><p><u>23</u>This saying therefore went out among the brothers,* that this disciple wouldn’t die. Yet Jesus didn’t say to him that he wouldn’t die, but, “If I desire that he stay until I come, what is that to you?” <u>24</u>This is the disciple who testifies about these things, and wrote these things. We know that his witness is true. <u>25</u>There are also many other things which Jesus did, which if they would all be written, I suppose that even the world itself wouldn’t have room for the books that would be written.</p><h5>*John 20:16 Rabboni is a transliteration of the Hebrew word for “great teacher.” <br />*John 20:16 or, Master <br />*John 20:29 TR adds “Thomas,” <br />*John 21:8 200 cubits is about 100 yards or about 91 meters <br />*John 21:23 The word for “brothers” here may be also correctly translated “brothers and sisters” or “siblings.”</h5>","path":"/350","Question1":"What God-given instructions do you note in this portion of Scripture?","Question2":"Put yourself in the original setting and creatively think about what it might have been like to receive this instruction. How does this relate to you where you live and work today?","Question3":"What God-inspired hope do you note in this portion of Scripture? How can you grow to have more of this in your own life?","Question4":"Commit yourself to tell a friend about what this hope means for you today.","prevPath":"/349","nextPath":"/351"}},"pathContext":{}}

